import uniSegmentedControl from '../../components/uni-segmented-control/uni-segmented-control.vue'
import uniCard from '../../components/uni-card/uni-card.vue'
import dragButton from '../../components/drag-button/drag-button.vue'
import cmdIcon from "../../components/cmd-icon/cmd-icon.vue"
import outData from '../../data/outStore.js'
import purchaseData from '../../data/purchase.js'
import sellData from '../../data/sell.js'
import exchangeData from '../../data/exchange.js'

export default {
	components: {
		uniSegmentedControl,
		uniCard,
		dragButton,
		cmdIcon
	},
	data() {
		return {
			items: ['出库','请购','销售','转库'],
			current: 0,
			//将data文件夹中的数据读入
			outList: outData.data,
			purchaseList: purchaseData.data,
			sellList: sellData.data,
			exchangeList: exchangeData.data,
			outFilterText: '',
			purchaseFilterText: '',
			sellFilterText: '',
			exchangeFilterText: ''
		}
	},
	computed: {
		// 单据列表
		outFilterList () {
			var arr = []
			this.outList.forEach((item) => arr.push(item))
			if (this.outFilterText) {
				arr = this.outList.filter(item => item.mso_orga.includes(this.outFilterText))
				if(arr.length === 0) {
					arr = this.outList.filter(item => item.mso_iden.includes(this.outFilterText))
				} 
				if(arr.length === 0) {
					arr = this.outList.filter(item => item.mso_remarks.includes(this.outFilterText))
				} 
				if(arr.length === 0) {
					arr = this.outList.filter(item => item.mso_warehouse.includes(this.outFilterText))
				} 
				if(arr.length === 0) {
					arr = this.outList.filter(item => item.mso_req_department.includes(this.outFilterText))
				}
				if(arr.length === 0) {
					arr = this.outList.filter(item => item.mso_creator.includes(this.outFilterText))
				}
			}
			return arr
		},
		purchaseFilterList () {
			var arr = []
			this.purchaseList.forEach((item) => arr.push(item))
			if (this.purchaseFilterText) {
				arr = this.purchaseList.filter(item => item.rp_orga.includes(this.purchaseFilterText))
				if(arr.length === 0) {
					arr = this.purchaseList.filter(item => item.rp_iden.includes(this.purchaseFilterText))
				} 
				if(arr.length === 0) {
					arr = this.purchaseList.filter(item => item.rp_remarks.includes(this.purchaseFilterText))
				} 
				if(arr.length === 0) {
					arr = this.purchaseList.filter(item => item.rp_type.includes(this.purchaseFilterText))
				} 
				if(arr.length === 0) {
					arr = this.purchaseList.filter(item => item.rp_req_department.includes(this.purchaseFilterText))
				}
				if(arr.length === 0) {
					arr = this.purchaseList.filter(item => item.rp_creator.includes(this.purchaseFilterText))
				}
			}
			return arr
		},
		sellFilterList () {
			var arr = []
			this.sellList.forEach((item) => arr.push(item))
			if (this.sellFilterText) {
				arr = this.sellList.filter(item => item.so_orga.includes(this.sellFilterText))
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_remarks.includes(this.sellFilterText))
				} 
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_iden.includes(this.sellFilterText))
				} 
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_warehouse.includes(this.sellFilterText))
				} 
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_type.includes(this.selFilterText))
				}
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_creator.includes(this.sellFilterText))
				}
				if(arr.length === 0) {
					arr = this.sellList.filter(item => item.so_custom.includes(this.sellFilterText))
				}
			}
			return arr
		},
		exchangeFilterList () {
			var arr = []
			this.exchangeList.forEach((item) => arr.push(item))
			if (this.exchangeFilterText) {
				arr = this.exchangeList.filter(item => item.str_orga.includes(this.exchangeFilterText))
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_remarks.includes(this.exchangeFilterText))
				} 
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_iden.includes(this.exchangeFilterText))
				} 
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_to.includes(this.exchangeFilterText))
				} 
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_from.includes(this.exchangeFilterText))
				} 
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_req_department.includes(this.exchangeFilterText))
				}
				if(arr.length === 0) {
					arr = this.exchangeList.filter(item => item.str_creator.includes(this.exchangeFilterText))
				}
			}
			return arr
		}
	},
	methods: {
		//切换tab
		onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
		},
		//判断订单状态
		judgeStatus(status) {
			if (status === "草稿") {
				return 0;
			}
			else if(status === "已审批") {
				return 1;
			}
			else if(status === "已关闭") {
				return 2;
			}
		},
		//查看订单详情
		viewDetail(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//新增单据
		newOrder(page) {
			if(page === 0) {
				uni.navigateTo({
				    url: '../user/phoneus',
				});
			} else if(page === 1) {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(page === 2) {
				uni.navigateTo({
				    url: '../user/setting',
				});
			} else if(page === 3) {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//删除单据
		deleteOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				                url: '../user/myinfo',
				            });
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//提交草稿
		commitOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				                url: '../user/myinfo',
				            });
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//编辑草稿
		editOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//关闭订单
		closeOrder(iden) {
			uni.showModal({
			    title: '提示',
			    content: '确认关闭请购单：'+iden+" ?",
			    success: function (res) {
			        if (res.confirm) {
			            uni.navigateTo({
			                url: '../user/myinfo',
			            });
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},

	}
	
	// onLoad: function() {   //登录检查函数
